import Vue from "vue";
import Router from "vue-router";
import Login from "./components/UserLogin.vue";
import AddModel from "./components/AddModel.vue";

Vue.use(Router);

export default new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes: [
        {
            path: "/UserLogin",
            name: "UserLogin",
            component: UserLogin
        },
        {
            path: "/AddModel",
            name: "AddModel",
            component: AddModel
        }
    ]
});